import React, { Component } from 'react';
import { connect } from 'react-redux';
import * as actions from '../../actions/actions';
import PanelTab from './paneltabs'
// import '../../stylessheet/components/sidepanel.css'

const mapStateToProps = state =>
    ({
        tabState: state.panelContentListReducer.tabState,
    })

const mapDispatchToProps = dispatch =>
    ({
        fetchTabStateApi() {
            dispatch(
                actions.sidePanelServiceAction()
            )
        }
    })

class Panel extends Component {

    componentWillMount() {
        this.props.fetchTabStateApi()

    }

    render() {
        console.log(this.props.tabState)
        const { tabState } = this.props
        return (
            <div id="sidebar-wrapper" style={{ marginTop: "60px" }}>
                <ul className="nav nav-tabs navbar-inverse " role="tablist">
                    {tabState.map((value, index) =>
                        <PanelTab key={index} {...value} />
                    )}
                </ul>
                <div className="tab-content">
                    <div role="tabpanel" className="tab-pane active" id="today">Today</div>
                    <div role="tabpanel" className="tab-pane" id="tasks">Tasks</div>
                    <div role="tabpanel" className="tab-pane" id="chat">Chat</div>
                </div>
            </div>
        );
    }
}

const SidePanel = connect(mapStateToProps, mapDispatchToProps)(Panel);
export default SidePanel;


