import React, { Component } from 'react';

class NavRightTab extends Component {
    render() {
        return (
            <ui className="nav navbar-nav navbar-right">
                <li id="menu-toggle"><a className="sidepanel-open-button"><i className="fa fa-outdent"></i></a></li>
            </ui>
        );
    }
}

export default NavRightTab;


