import React from 'react';

const PanelTab = ({name}) => {
    if({name}.name==="TODAY"){
        return <li role="presentation" className="active">
            <a href="#today" aria-controls="today" role="tab" data-toggle="tab">{name}</a>
            </li>
    }else if({name}.name==="TASKS"){
        return <li role="presentation"><a href="#tasks" aria-controls="tasks" role="tab" data-toggle="tab">{name}</a></li>
    }else {
        return <li role="presentation"><a href="#chat" aria-controls="chat" role="tab" data-toggle="tab">{name}</a></li>
    }
}

export default PanelTab;


